.filter {

	// .filter__body

	&__body {
		display: flex;
		align-items: center;
		gap:35px;
	}

	// .filter__label

	&__label {
	}

	// .filter__buttons

	&__buttons {
		display: flex;
		gap:5px;
	}

	// .filter__button

	&__button {

		width: 75px;
		height: 30px;
		background: #FFFFFF;
		box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
		transition: all 0.3s ease 0s;
		font-size: 12px;
		line-height: 17px;
		text-align: center;
		color: #000000;
		cursor: pointer;
		outline: solid 1px transparent;
		&:first-child{
			border-radius: 4px 0px 0px 4px;
		}
		&:last-child{
			border-radius: 0px 4px 4px 0px;
		}
		&:hover{
			outline: solid 1px #a4a4a4;
		}
		&.active{
			background: #a4a4a4;
		}
	}
}
