





.header {
	padding: 30px 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 50;
	color: #FFFFFF;
	text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	// .header__container

	&__container {
		min-height: 40px;
	}

	// .header__body

	&__body {
		
		//max-width: 355px;
	}

	// .header__menu

	&__menu {
	}
}


.menu {
	display: flex;
	// .menu__body

	&__body {
		display: flex;
	}


	// .menu__list

	&__list {
		display: flex;
		gap:40px;
		align-self: end;
	}
	// .menu__item

	&__item {
		font-size: 12px;
		line-height: 17px;
		cursor: pointer;
		transition: all 0.3s ease 0s;
		&:hover{
			transform: scale(1.2);
		}

	}
}
