.best {
	//background: url('../../../public/images/coffee-best/best-bg.jpg') center center/cover no-repeat;

	// .about__container

	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	// .about__body

	&__body {
		padding-top: 80px;
		padding-bottom: 100px;&:not(:last-child) {margin-bottom: 40px;}
		
		max-width: 800px;
	}

	// .about__title

	&__title {
		text-align: center;
		font-weight: 700;
		font-size: 24px;
		line-height: 35px;
		&:not(:last-child) {margin-bottom: 20px;}
	}
	&__cards{
		width: 100%;
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		column-gap:70px;
		row-gap: 60px;
	}
}
