.footer {
	background-color: #fff;
	padding: 30px 0;
	// .footer__container

	&__container {
		min-height: 40px;
	}

	// .footer__body

	&__body {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	// .footer__menu

	&__menu {
	}
	
}
.menu {
	display: flex;
	&:not(:last-child) {margin-bottom: 30px;}
	// .menu__body

	&__body {
		display: flex;
	}


	// .menu__list

	&__list {
		display: flex;
		gap:40px;
		align-self: end;
	}
	// .menu__item

	&__item {
		font-size: 12px;
		line-height: 17px;
		cursor: pointer;
	}

	&__devider {

	}

}

