.banner {
	position: relative;
	width: 100%;
	max-height: 640px;
	min-height: 260px;
	overflow: hidden;
	// .banner__image

	&__image {
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: -1;
		img{
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		}
	}

	// .banner__container

	&__container {
		//z-index: 3;
		
	}

	// .banner__body

	&__body {
		display: flex;
		flex-direction: column;
		align-items: center;
		color: #FFFFFF;
		padding-top: 180px;
		padding-bottom: 200px;
	}

	// .banner__title

	&__title {
		font-weight: 700;
		font-size: 40px;
		line-height: 58px;
		text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		&:not(:last-child) {margin-bottom: 20px;}

	}

	// .banner__devider

	&__devider {
		&:not(:last-child) {margin-bottom: 40px;}
	}

	// .banner__text

	&__text {
		display: flex;
		flex-direction: column;
		align-items: center;
		font-weight: 700;
		font-size: 24px;
		line-height: 35px;
		p{
			&:not(:last-child) {margin-bottom: 20px;}
		}
		&:not(:last-child) {margin-bottom: 20px;}
	}

	// .banner__button

	&__button {
		width: 120px;
		height: 30px;
		border: 1px solid #FFFFFF;
		filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
		border-radius: 3px;
		font-weight: 700;
		font-size: 14px;
		line-height: 20px;
		&:not(:last-child) {margin-bottom: 40px;}
	}
}
