.gallery {
	background-color: #fff;
	padding-bottom: 30px;
	// .gallery__devider

	&__devider {
		text-align: center;
		padding-bottom: 60px;
	}

	// .gallery__container

	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	// .gallery__header

	&__header {
		display: flex;
		gap:80px;
		&:not(:last-child) {margin-bottom: 60px;}
	}

	// .gallery__search

	&__search {
	}

	// .gallery__filter

	&__filter {
	}

	// .gallery__body

	&__body {
		max-width: 800px;
	}

	// .gallery__cards

	&__cards {
		width: 100%;
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		column-gap:70px;
		row-gap: 60px;
	}
}
