.card {
	max-width:220px;
	min-height: 240px;
	background: rgba(255, 255, 255, 0.65);
	border-radius: 8px;
	padding: 22px 20px;
	display: flex;
	align-items: flex-end;
	justify-content: center;
	cursor: pointer;
	transition: all 0.3s ease 0s;
	&:hover{
		outline-color: black;
		outline: solid 1px black;
	}


	
	// .card__body

	&__body {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 10px;
		width: 100%;
		height: 100%;
	}

	// .card__image

	&__image {
		flex: 1 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		overflow: hidden;
		min-width: 150px;
		min-height: 130px;
		img {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			object-fit: cover;
		}
	}

	// .card__title

	&__title {
		line-height: 20px;
	}
	&__country {
		align-self: flex-end;
	}

	// .card__price

	&__price {
		align-self: flex-end;
		font-weight: 700;
		line-height: 20px;
	}
}
