.about {
	background-color: #fff;
	// .about__container

	&__container {
		display: flex;
		align-items: center;
		justify-content: center;


	}

	// .about__body

	&__body {
		padding-top: 80px;
		padding-bottom: 100px;
		text-align: center;
		max-width: 589px;


	}
	&__image{
		padding-right:80px ;
		img{
			box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25)
		}


	}

	// .about__title

	&__title {
		font-weight: 700;
		font-size: 24px;
		line-height: 35px;
		&:not(:last-child) {margin-bottom: 20px;}
	}

	// .about__devider

	&__devider {
		&:not(:last-child) {margin-bottom: 40px;}
	}
	// .about__country

	&__country {
		line-height: 20px;
		span{
			&:first-child{
				font-weight: 700;
			}
		}
		&:not(:last-child) {margin-bottom: 16px;}
		text-align: left;
	}

	// .about__text

	&__text {
		line-height: 20px;
		text-align: center;
		span{
			&:first-child{
				font-weight: 700;
			}
		}
		p{
			&:not(:last-child) {margin-bottom: 20px;}
		}
		&:not(:last-child) {margin-bottom: 20px;}
	}

	&__price {
		font-weight: 400;
		font-size: 24px;
		line-height: 35px;
		span{
			&:first-child{
				font-size: 14px;
				line-height: 20px;
				font-weight: 700;
			}
		}
		text-align: left;
	}
}
