
.search {

	// .search__body

	&__body {
	}

	// .search__label

	&__label {
		padding-right: 20px;
	}
	// .search__input

	&__input {
		&::placeholder{
			font-size: 12px;
			line-height: 17px;
			text-align: center;
			color: rgba(0, 0, 0, 0.5);
		}
		&:focus{
			outline-color: black;
		}
		padding: 0 10px;
		width: 180px;
		height: 30px;
		background: #FFFFFF;
		box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
		border-radius: 4px;
	}
}
